@import "../../assets/styles/colors.scss";

@keyframes marqueeLeft {
  0% {
    left: 0;
  }

  100% {
    left: -100%;
  }
}

.crypto-statistics-wrapper {

  .crypto-statistics-wrapper__container {
    overflow: hidden;

    .crypto-statistics-wrapper__block {
      height: 48px;
      overflow: hidden;
      box-sizing: border-box;
      position: relative;

      .crypto-statistics-wrapper__line {
        display: flex;
        position: absolute;
        width: 100%;
        align-items: center;
        white-space: normal;
        .wrapper__line-span{
          display: flex;
          float:left;
          width:100%;
        }
      }

      .to-left {
        //animation: marqueeLeft 25s linear infinite;
      }
    }
  }


}

.running-line__element {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
  width: auto;

  .running-line__name {
    font-family: 'Poppins';
    font-size: 14px;
    font-weight: 700;
    color: #3F3F3F;
    text-transform: uppercase;
    white-space: nowrap;
  }

  .running-line__price {
    color: #000;
    font-family: 'Poppins';
    font-size: 14px;
    font-weight: 700;
    margin: 0 0.5rem;
  }

  .running-line__discount {
    color: #3F3F3F;
    font-family: 'Poppins';
    font-size: 14px;
    font-weight: 700;
  }

  &::after {
    content: "";
    width: 5px;
    height: 5px;
    background: #3F3F3F;
    border-radius: 100px;
    margin: 0 2rem;
  }

}