@import '../../../assets/styles/colors.scss';

.login-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;

    .block__inputs {
        position: relative;
        display: flex;
        flex-direction: column;

        .block__inputs-input-password {
            position: relative;

            svg {
                path {
                    stroke: #4F5665;
                }
            }
        }

        .block__inputs-input__eye {
            position: absolute;
            background: none;
            border: none;
            right: 2%;
            top: 15%;
            transform: translateY(50%);
            cursor: pointer;
        }

        .block__inputs-input {
            height: 54px;
            width: 328px;
            padding-left: 1rem;
            font-family: 'Poppins';
            font-size: 16px;
            border: 1px solid black;
            border-radius: 60px;
            margin-bottom: 16px;

            &:focus {
                border: 1px solid #B3AAFE;
            }

            &::placeholder {
                font-family: 'Poppins';
                color: #4F5665;
            }
        }


        .block__inputs__phone {
            display: flex;
            align-items: center;
            border: 1px solid #000;
            padding: 6px 16px;
            border-radius: 100px;
            width: calc(328px - 32px );
            height: calc(54px - 12px );
            margin-bottom: 16px;
            .block__inputs__phone__country-code {
                display: flex;
                align-items: center;

                font-family: 'Poppins';
                font-size: 16px;
                font-weight: 600;
                line-height: 24px;
                letter-spacing: 0em;
                text-align: justify;

                .block__inputs__phone__country-code__plus {

                }

                .block__inputs__phone__country-code__code {
                    margin: 0 4px;
                }
            }

            .block__inputs-input{
                height: calc(54px - 12px );
                margin-left: 12px;
                width: 100%;
                padding: 0;
                padding-left: 1rem;
                font-family: "Poppins";
                font-size: 16px;
                border: none ;
                border-left: 1px solid #B4B4B4;
                border-radius: 0;
                margin-bottom: 0;

                &::placeholder{
                    color:#B4B4B4;
                }

                &:focus{
                    outline: none;
                }
            }
        }

    }

    .block__button {
        margin-bottom: 30px;

        .section-btn {
            width: 348px;
        }
    }

    .block__forgot {
        margin-bottom: 24px;

        .block__forgot-p {
            margin: 0;
            font-family: 'Poppins';
            font-size: 14px;
            font-weight: 400;
            line-height: 17px;
            letter-spacing: 0em;
            padding-bottom: 4px;
            border-bottom: 1px solid #3F3F3F;
            color: #3F3F3F;
            cursor: pointer;
        }
    }
}