.user-profile-page{
    display: flex;
    margin: 4% 10% 4% 8%;
}

.user-profile-section{
   
    width: 100%;
    position: relative;
    width: auto;
    display: flex;
    flex-direction: column;


    .user-profile-settings{
        display: flex;
        align-items: center;

        .user-profile-container{
            display: flex;
            align-items: center;
            justify-content: space-between;
            border: 1px solid black;
            border-radius: 8px;
            width: calc(400px - 4rem);
            padding: 1rem 2rem; 
        }

        .user-profile-name{
            display: flex;
            align-items: center;
            span{
                margin-left: 1rem;
            }
        }

        svg{
            cursor: pointer;
        }
    }
    .user-profile-tabs{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        border: 1px solid black;
        border-radius: 8px;
        width: 400px;
        padding: 1rem 0;
        margin: 1rem 0;
        a{
            font-family: 'Poppins';
            font-size: 20px;
            font-weight: 500;
            display: flex;
            align-items: center;
            margin: 1.15rem 2rem;
            width: 100%;
            transition: all 0.2s;
            svg{
                margin-right: 0.7rem;
                path{
                    transition: all 0.2s;
                }
              
            }

            &:hover{
                color:#B4B4B4;
                transition: all 0.2s;
                path{
                    filter:invert(0.7);
                    transition: all 0.2s;
                }
            }

        }
        

    }
    .user-profile-logout{
        display: flex;
        align-items: center;
        font-family: 'Poppins';
        font-size: 20px;
        font-weight: 400;
        cursor: pointer;

        .user-profile-container{
            display: flex;
            align-items: center;
            border: 1px solid black;
            border-radius: 8px;
            width: calc(400px - 4rem);
            padding: 1rem 2rem;
            svg{
                margin-right: 0.5rem;
            }
        }
    }  
    .active-profile-link{
        color:#B4B4B4;
        path{
            filter:invert(0.7)
        }
    }

    
}