.review-container{
    width: 340px;
    height: 170px;
    border-radius: 10px;
    border: 1px solid black;
    padding: 30px;
    
    .review-container__header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
        .review-container__header__user{
            display: flex;
            align-items: center;
            .review-container__header__user__fake-img{
                width: 50px;
                height: 50px;
                border-radius: 100px;
                background: #E7EAED;
            }
            .review-container__header__user__info{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin-left: 20px;
                .review-container__header__user__name{
                    font-family: 'Poppins';
                    font-size: 18px;
                    font-weight: 600;
                    line-height: 30px;
                    letter-spacing: 0em;
                    
                }
                .review-container__header__user__place{
                    font-family: 'Poppins';
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 17px;
                    letter-spacing: 0em;
                }
            }
        }
        .review-container__header__rating{
            display: flex;
            align-items: center;
            svg{
                margin-left: 10px;
            }
        }
    }

    .review-container__content{
        .review-container__text{
            font-family: 'Poppins';
            font-size: 14px;
            font-weight: 400;
            text-align: justify;
            margin: 0;

        }
    }
}