.profile-safety-page {
    margin-left: 6%;
    width: 100%;

    .profile-page__section {
        position: relative;
        width: 100%;
        margin-bottom: 30px;
    }

    .profile-page__block-title {
        margin: 0;
        text-transform: uppercase;
    }

    .profile-page__content__switch {
        display: flex;
        align-items: center;
        border: 1px solid #000;
        border-radius: 8px;
        padding: 1.5rem 2rem;
        margin-top: 16px;

        .profile-personal-switch {
            position: relative;
            display: inline-block;
            width: 72px;
            height: 32px;

            input{
                display: none;
            }

            .slider {
                position: absolute;
                cursor: pointer;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                border: 1px solid #000;
                width: 72px;
                height: 32px;
                -webkit-transition: .4s;
                transition: .4s;
            }

            .slider:before {
                position: absolute;
                content: "";
                height: 26px;
                width: 26px;
                left: 4px;
                bottom: 3px;
                background-color: #000;
                -webkit-transition: .4s;
                transition: .4s;
            }

            input:checked+.slider:before {
                left: 16px;
                background-color: #fff;
            }

            input:checked+.slider {
                background-color: #000;
            }

            input:focus+.slider {
                box-shadow: 0 0 1px #000;
            }

            input:checked+.slider:before {
                -webkit-transform: translateX(26px);
                -ms-transform: translateX(26px);
                transform: translateX(26px);
            }

            /* Rounded sliders */
            .slider.round {
                border-radius: 34px;
            }

            .slider.round:before {
                border-radius: 50%;
            }
        }

        .profile-page__content__switch__info {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-left: 2.7rem;

            .profile-page__content__switch__title {
                font-family: 'Poppins';
                font-size: 20px;
                font-weight: 600;

            }

            .profile-page__content__switch__text {
                font-family: 'Poppins';
                font-size: 16px;
                font-weight: 400;
            }
        }
    }

    .profile-page__content__input {
        position: relative;
        width: 100%;
        margin-top: 16px;

        .profile-personal-input__eye{
            position: absolute;
            top:40%;
            right:2%;
            border: none;
            background: transparent;
        }

        .profile-personal-input {
            width: calc(100% - 20px - 25px);
            height: auto;
            padding: 40px 20px 15px 25px;
            border: 1px solid #000;
            border-radius: 8px;
            font-family: 'Poppins';
            font-size: 18px;
            font-weight: 600;

            &::placeholder {
                font-family: 'Poppins';
                font-size: 20px;
                font-weight: 600;
            }

        }

        .profile-personal-label {
            position: absolute;
            left: 25px;
            top: 16px;
            transition: top 0.5s;
            font-family: 'Poppins';
            font-size: 16px;
            font-weight: 500;
            transition: all .3s;
        }

        .profile-personal-input:not(:focus)~.profile-personal-label {
            top: 28px;
            font-size: 18px;
            transition: all .3s;
        }

        .profile-personal-input:not(:invalid)~.profile-personal-label {
            top: 16px;
            font-size: 16px;
            transition: all .3s;
        }

    }
}