@import './colors.scss';


html{
  margin: 0;
  padding: 0;
  font-family: 'Poppins' !important;
}

body{
  margin: 0;
  padding: 0;
  background: $primary-bg-color;
  font-family: 'Poppins' !important;
  font-size: 16px;
}

a{
  text-decoration: none;
  color:black;
}

ul{
  text-decoration: none;
  list-style: none;
  padding: 0;
  margin: 0;
}

.overlay{
  position: fixed;
  z-index: 99;
  top:0;
  left: auto;
  right: auto;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(6px);
}

.section-h1{
  font-family: 'Poppins';
  margin-top: 0;
  margin-bottom: 0;
  font-size: 50px;
  font-weight: 600;
  line-height: 70px;
}

.section-p{
  font-family: 'Poppins';
  font-size:18px;
  font-weight: 400;
  line-height: 30px;
  margin-top: 0;
  margin-bottom: 5%;
  color:#4F5665;
}

.section-btn{
  font-family: 'Poppins';
  font-size:16px;
  font-weight: 700;
  color: #fff;
  background: #000;
  border:1px solid transparent;
  border-radius:60px;
  width: 250px;
  height: 60px;
  cursor: pointer;

  &:hover{
    border:1px solid #000;
    background: #fff;
    color:#000;
    svg{
      path{
        fill:#000;
      }
    }
  }
}

.section-link{
  
  display: flex;
  align-items: center;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid $secondary-font-color;
  a{
      font-family: 'Poppins';
      color: $secondary-font-color;
  }
  svg{
      margin-left: 0.5rem;
  }
}


.unselectable {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */
  -khtml-user-select: none;    /* Konqueror */
  -moz-user-select: none;      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge */
  user-select: none;           /* Non-prefixed version, currently
                                  not supported by any browser */
}

::-webkit-scrollbar {
  width: 14px; /*    */
} 
::-webkit-scrollbar-track {
  background: #DBDBDB; /*     */
  box-shadow: 0 0 2px rgba(0, 0, 0, .2) inset; /*    */
}
::-webkit-scrollbar-thumb {
  background: #C2BEBE; /*     */
  border-radius: 8px; /*   */
  border: 3px solid #DBDBDB; /* ,     */
}



::-webkit-scrollbar-thumb:hover {
  background: #767676; /*      */
}


/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}