.profile-data-page {
    margin-left: 6%;
    width: 100%;

    .profile-page__section{
        position: relative;
        width: 100%;
        margin-bottom: 30px;
    }

    .profile-page__block-title {
        margin: 0;
        text-transform: uppercase;
    }

    .profile-page__content__input {
        position: relative;
        width: 100%;
        margin-top: 16px;

        .profile-personal-input {
            width: calc(100% - 20px - 25px);
            height: auto;
            padding: 40px 20px 15px 25px;
            border: 1px solid #000;
            border-radius: 8px;
            font-family: 'Poppins';
            font-size: 18px;
            font-weight: 600;

            &::placeholder {
                font-family: 'Poppins';
                font-size: 20px;
                font-weight: 600;
            }

        }

        .profile-personal-label {
            position: absolute;
            left: 25px;
            top: 16px;
            transition: top 0.5s;
            font-family: 'Poppins';
            font-size: 16px;
            font-weight: 500;
            transition: all .3s;
        }

        .profile-personal-input:not(:focus)~.profile-personal-label {
            top: 28px;
            font-size: 18px;
            transition: all .3s;
        }

        .profile-personal-input:not(:invalid)~.profile-personal-label {
            top: 16px;
            font-size: 16px;
            transition: all .3s;
        }

    }

    .profile-page__dob__content{
        display: flex;
        align-items: center;
        width: 100%;

        .profile-page__content__input {
            position: relative;
            display: flex;
            width: auto;
            margin-top: 16px;
            
            &:not(:last-child){
                margin-right: 20px;
            }

           
            .profile-personal-input {
                width: 18.375rem;
                height: auto;
                padding: 40px 20px 15px 25px;
                border: 1px solid #000;
                border-radius: 8px;
                font-family: 'Poppins';
                font-size: 18px;
                font-weight: 600;
    
                &::placeholder {
                    font-family: 'Poppins';
                    font-size: 20px;
                    font-weight: 600;
                }
    
            }
    
            .profile-personal-label {
                position: absolute;
                left: 25px;
                top: 16px;
                transition: top 0.5s;
                font-family: 'Poppins';
                font-size: 16px;
                font-weight: 500;
                transition: all .3s;
            }
    
            .profile-personal-input:not(:focus)~.profile-personal-label {
                top: 28px;
                font-size: 18px;
                transition: all .3s;
            }
    
            .profile-personal-input:not(:invalid)~.profile-personal-label {
                top: 16px;
                font-size: 16px;
                transition: all .3s;
            }
    
        }
    }
}