.template-blog-post{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    background: #E9ECEF;
    border-radius: 16px;
    padding: 45px 48px;
    height: calc(415px - 90px);
    cursor: pointer;

    .template-blog-post__icons{
        svg{
            margin-right: 1.5rem;
        }
        
    }
    .template-blog-post__title{
        margin: 24px 0;
        font-family: 'Poppins';
        font-size: 24px;
        font-weight: 600;
        line-height: 36px;
        letter-spacing: 0em;
        text-align: left;
        max-width: 430px;
    }
    .template-blog-post__creator{
        font-family: 'Poppins';
        font-size: 16px;
        font-weight: 600;
        line-height: 24px;
        letter-spacing: 0em;
        text-align: left;
        color:#706E6E;
    }
}