.blog-page{
    margin: 4% 8%;

    .blog-posts{
        display: grid;
        column-gap: 16px;
        row-gap: 24px;
        grid-template-columns: 494px 596px 494px;
        grid-template-rows: auto auto auto;

        .post5, .post9{
            grid-column: span 2;
        }
        
        margin-bottom: 3%;
    }

    .blog-buttons{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .blog-buttons__show-more{
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid #000;
            border-radius: 30px;
            padding: 1rem 1rem ;
            width: 178px;
            font-family: 'Poppins';
            font-size: 16px;
            font-weight: 600;
            cursor: pointer;
            svg{
                margin-right: 1rem;
            }
        }
    }

}