.post-page{
    margin: 4% 8%;

    .post-page__header{
        display: flex;
        align-items: center;
        justify-content: space-between;

        .post-page__header__content__icons{
            svg{
                cursor: pointer;
                margin-right: 1rem;
            }
           
        }

        .post-page__header__img{
            width: 595px;
            height: 486px;
            border-radius: 16px;
            background: #E9ECEF;
            width: 45%;
        }
    
        .post-page__header__content{
            width: 47%;
            .post-page__header__content__title{
                font-family: 'Poppins';
                font-size: 48px;
                font-weight: 300;
                line-height: 72px;
                letter-spacing: 0em;
                text-align: left;
                margin: 32px 0 ;
            }
            .post-page__header__content__info{
                display: flex;
                align-items: flex-end;
                justify-content: space-between;
                .post-page__header__content__info-creator{
                    font-family: 'Poppins';
                    font-size: 24px;
                    font-weight: 500;
                    line-height: 36px;
                    letter-spacing: 0em;
                    text-align: left;
                    color:#565656;
                    span{
                        font-weight: 600;
                    }
                }
                .post-page__header__content__info-time{
                    font-family: 'Poppins';
                    font-size: 24px;
                    font-weight: 500;
                    line-height: 36px;
                    letter-spacing: 0em;
                    text-align: left;

                    color:#565656;
                    svg{
                        margin-right: 1rem;
                    }
                }
            }

        }
        margin-bottom: 6rem;
    }

    .post-page__content{
        margin: 0 15%;
        h3{
            font-family: 'Poppins';
            font-size: 24px;
            font-weight: 600;
            line-height: 30px;
            letter-spacing: 0em;
            text-align: left;
            text-transform: uppercase;
        }

        p{
            font-family: 'Poppins';
            font-size: 18px;
            font-weight: 400;
            line-height: 30px;
            letter-spacing: 0em;
            text-align: justify;        
        }
    }
}

